<template>
  <div class="role-wrap">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>设置</el-breadcrumb-item>
      <el-breadcrumb-item>角色管理</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="manage-cont">

      <setupNav></setupNav>

      <div class="manage-nr">
        <el-card class="box-card">
          <el-button type="primary" size="small" @click="addRole">添加业务角色</el-button>
          <el-button type="primary" size="small">删除</el-button>
          <el-input placeholder="请输入内容" v-model="input" size="small" class="input-with-seach ml20">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
          <el-table
            class="mt20"
            ref="multipleTable"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange"
            stripe>
            <el-table-column
              type="selection"
              prop="id"
              width="50">
            </el-table-column>
            <el-table-column
              label="序号"
              type="index"
              width="50">
            </el-table-column>
            <el-table-column
              label="角色名称"
              prop="name"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="角色类型"
              prop="type"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="继承角色"
              prop="inherit"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="角色描述"
              prop="describe"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              label="用户数"
              prop="num"
              show-overflow-tooltip>
            </el-table-column>
          </el-table>
        </el-card>
      </div>

    </div>

    <el-dialog
      title="添加业务角色"
      :visible.sync="addRoledialog"
      width="60%">
      <el-form ref="addRoleFrom" :model="addRoleFrom" label-width="120px">
        <el-form-item label="角色名称：">
          <el-input v-model="addRoleFrom.name"></el-input>
        </el-form-item>
        <el-form-item label="角色描述：">
          <el-input type="textarea" v-model="addRoleFrom.desc"></el-input>
        </el-form-item>
      </el-form>
      <div class="popcont">
        <div class="popcont-l">
          <el-tree :data="treeData" :props="treeProps" @node-click="handleNodeClick"></el-tree>
        </div>
        <div class="popcont-r">
          <el-table
            :data="popTableData"
            tooltip-effect="dark"
            style="width: 100%"
            border>
            <el-table-column
              label="菜单名称"
              prop="menu"
              width="100">
            </el-table-column>
            <el-table-column
              label="功能名称"
              prop="funct"
              width="100">
            </el-table-column>
            <el-table-column label="操作功能">
              <template slot-scope="scope">
                <el-checkbox-group v-model="checkList">
                  <el-checkbox v-for="popCheck in scope.row.check" :label="popCheck.id" :key="popCheck.id">{{popCheck.name}}</el-checkbox>
                </el-checkbox-group>
              </template>
            </el-table-column>

          </el-table>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addRoledialog = false">取 消</el-button>
        <el-button type="primary" @click="addRoleSave">保 存</el-button>
      </span>
    </el-dialog>

  </div>


</template>

<script type="text/javascript">
  import setupNav from 'components/setup/nav'
  export default {
    name: 'home',
    data(){
      return {
        addRoleFrom:{
          name:'',
          desc:''
        },
        addRoledialog:false,
        input:'',
        tableData: [{
          id:1,
          name: '普通业务用户',
          type: '业务角色',
          inherit:'无',
          describe:'普通角色，拥有普通查询功能权限',
          num:'34'
        },{
          id:2,
          name: '普通数据用户',
          type: '数据角色',
          inherit:'四川用户',
          describe:'特殊数据角色，可查询眉山接入数据，继承四川角色，可查询四川接入数据',
          num:'4'
        }],
        popTableData: [{
          menu:'区域碰撞',
          funct: '区域碰撞',
          check: [{id:11,name:'QQ'},{id:12,name:'区域碰撞'}]
        },{
          menu:'区域碰撞2',
          funct: '区域碰撞2',
          check: [{id:13,name:'QQQ'},{id:14,name:'区域碰撞2'}]
        }],
        checkList:[],
        page:{
          currentPage:1,
          sizes:[10, 20],
          size:10,
          total:20
        },
        treeData: [{
          label: '四川省公安厅情报总队',
          children: [{
            label: '眉山市局情报支队',
            children: [{
              label: '青神县公安局'
            }]
          }]
        },{
          label: '内蒙古省公安厅',
          children: [{
            label: '呼和浩特铁路公安局'
          }]
        },{
          label: 'XXX省公安厅'
        }],
        treeProps: {
          children: 'children',
          label: 'label'
        },
      }
    },
    methods:{
      handleSelectionChange(val) {
        console.log(val)
        this.multipleSelection = val;
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      addRole(){
        this.addRoledialog = true;
      },
      handleNodeClick(data) {
        console.log(data);
      },
      addRoleSave(){
        console.log(this.checkList.join())
        this.addRoledialog = false;
      }
    },
    components:{
      setupNav
    }
   }
</script>

<style lang="scss" scoped>
  @import "~assets/css/lib-variable";
  @import "~assets/css/lib-mixin";

  .role-wrap{
    width:  $site-width;
    min-width: $site-minwidth;
    min-height:$site-minheight;
    padding:0 20px;
    box-sizing:border-box;
  }
  .popcont{
    overflow:hidden;margin-top:20px;
    .popcont-l{float:left;width:30%;}
    .popcont-r{margin-left:32%;}
  }
</style>



