import Vue from 'vue'
import Router from 'vue-router'
import Home from 'pages/home/home'
import page1 from 'pages/page1/page1'
import page2 from 'pages/page2/page2'
import page3 from 'pages/page3/page3'
import userManage from 'pages/setup/user'
import organization from 'pages/setup/organization'
import role from 'pages/setup/role'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',  //路由默认跳转
      redirect: 'home'
    }, {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path: '/page1',
      name: 'page1',
      component: page1
    },
    {
      path: '/page2',
      name: 'page2',
      component: page2
    },
    {
      path: '/page3',
      name: 'page3',
      component: page3
    },
    {
      path: '/userManage',
      name: 'userManage',
      component: userManage
    },
    {
      path: '/organization',
      name: 'organization',
      component: organization
    },
    {
      path: '/role',
      name: 'role',
      component: role
    }
  ],
  linkActiveClass: 'active'
})



