<template>
  <div class="organ-wrap">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>设置</el-breadcrumb-item>
      <el-breadcrumb-item>组织机构管理</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="manage-cont">

      <setupNav></setupNav>

      <div class="manage-nr">
        <div class="organ-tree">
          <el-card class="box-card">
            <el-tree :data="treeData" :props="treeProps" @node-click="handleNodeClick"></el-tree>
          </el-card>
        </div>
        <div class="organ-cont">
          <el-card class="box-card">
            <el-button type="primary" size="small" @click="addOrgan">添加组织机构</el-button>
            <el-button type="primary" size="small">删除组织机构</el-button>
            <el-input placeholder="请输入内容" v-model="input" class="input-with-seach ml20" size="small">
              <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
            <el-table
              class="mt20"
              ref="multipleTable"
              :data="tableData"
              tooltip-effect="dark"
              style="width: 100%"
              @selection-change="handleSelectionChange"
              stripe>
              <el-table-column
                type="selection"
                prop="id"
                width="50">
              </el-table-column>
              <el-table-column
                label="序号"
                type="index"
                width="50">
              </el-table-column>
              <el-table-column
                label="名称"
                prop="name"
                show-overflow-tooltip>
              </el-table-column>
              <el-table-column
                label="组织机构类型"
                prop="type"
                show-overflow-tooltip>
              </el-table-column>
              <el-table-column
                label="直属成员"
                prop="directly"
                show-overflow-tooltip>
              </el-table-column>
              <el-table-column
                label="所有成员"
                prop="allMember"
                show-overflow-tooltip>
              </el-table-column>
              <el-table-column
                label="所有子机构"
                prop="allOrgan"
                show-overflow-tooltip>
              </el-table-column>
            </el-table>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="page.currentPage"
              :page-sizes="page.sizes"
              :page-size="page.size"
              layout="total, sizes, prev, pager, next, jumper"
              :total="page.total">
            </el-pagination>
          </el-card>
        </div>
      </div>

    </div>

    <el-dialog
      title="添加组织机构"
      :visible.sync="addOrgandialog"
      width="50%"
      :before-close="handleClose">
      <el-form ref="addOrganFrom" :model="addOrganFrom" label-width="120px">
        <el-form-item label="组织机构名称：">
          <el-input v-model="addOrganFrom.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            expand-trigger="click"
            placeholder="选择上级组织机构"
            :options="options"
            change-on-select
            v-model="addOrganFrom.selectedOptions1"
            @change="handleChange">
          </el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addOrgandialog = false">取 消</el-button>
        <el-button type="primary" @click="addOrgandialog = false">保 存</el-button>
      </span>
    </el-dialog>

  </div>


</template>

<script type="text/javascript">
  import setupNav from 'components/setup/nav';
  import {organHttp} from '../../api/setupHttp.js';
  export default {
    name: 'home',
    data(){
      return {
        addOrgandialog: false,
        addOrganFrom:{
          name:'',
          selectedOptions1:[]
        },
        options: [{
          value: 'zhinan',
          label: '指南',
          children: [{
            value: 'shejiyuanze',
            label: '设计原则',
            children: [{
              value: 'yizhi',
              label: '一致'
            }, {
              value: 'fankui',
              label: '反馈'
            }, {
              value: 'xiaolv',
              label: '效率'
            }, {
              value: 'kekong',
              label: '可控'
            }]
          }, {
            value: 'daohang',
            label: '导航',
            children: [{
              value: 'cexiangdaohang',
              label: '侧向导航'
            }, {
              value: 'dingbudaohang',
              label: '顶部导航'
            }]
          }]
        }],
        input:'',
        treeData: [{
          label: '四川省公安厅情报总队',
          children: [{
            label: '眉山市局情报支队',
            children: [{
              label: '青神县公安局'
            }]
          }]
        },{
          label: '内蒙古省公安厅',
          children: [{
            label: '呼和浩特铁路公安局'
          }]
        },{
          label: 'XXX省公安厅'
        }],
        treeProps: {
          children: 'children',
          label: 'label'
        },
        tableData: [{
          id:1,
          name: '梨园镇公安局',
          type: '机关单位',
          directly:'2',
          allMember:'3',
          allOrgan:'0'
        },{
          id:2,
          name: '宋庄镇公安局',
          type: '组织机构',
          directly:'3',
          allMember:'12',
          allOrgan:'5'
        }],
        page:{
          currentPage:1,
          sizes:[10, 20],
          size:10,
          total:20
        }
      }
    },
    created:function(){
      this.initData();
    },
    methods:{
      initData(){
        this.getList();
      },
      getList(){
        organHttp.getList().then((result)=>{
          console.log(result.returnData.list)
//          this.tableData = result.returnData.list;
        }).catch(() => {});
      },
      handleNodeClick(data) {
        console.log(data);
      },
      handleSelectionChange(val) {
        console.log(val)
        this.multipleSelection = val;
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      addOrgan(){
        this.addOrgandialog = true;
      },
      handleClose(){
        this.addOrgandialog = false;
      },
      handleChange(value) {
        console.log(value);
      },
    },
    components:{
      setupNav
    }
   }
</script>

<style lang="scss" scoped>
  @import "~assets/css/lib-variable";
  @import "~assets/css/lib-mixin";

  .organ-wrap{
    width:  $site-width;
    min-width: $site-minwidth;
    min-height:$site-minheight;
    padding:0 20px;
    box-sizing:border-box;
    .organ-tree{float:left;width:250px;}
    .organ-cont{margin-left:260px;}
  }

</style>



